.header {
  max-width: 880px;
  height: auto;
  padding-top: 45px;
  padding-bottom: 41px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
}

@media (max-width: 800px) {
  .header {
    padding-top: 28px;
    padding-left: 27px;
    padding-bottom: 32px;
    max-width: 320px;
  }
}
